<template>
  <div>
    <el-row :gutter="10">
      <el-col :span="6">
        <Assembly
          :drag2="drag2"
          @addcom="addcom"
          @setcom="setcom"
          @setdrag="setDrag"
          @deletecom="deleteComponent"
          @reset="reset"
        />
      </el-col>
      <el-col :span="12">
        <Forms
          ref="forms"
          :from-data="data"
          :form-setting="formSetting"
          :drag1="drag1"
          :drag2="drag2"
          :com="com"
          @setdrag="setDrag"
          @reset="reset"
          @setdelcom="setdelcom"
          @addcom="addcom"
          @exchange="exchange"
          @setformcom="setformcom"
          @setDialogVisible="dialogVisible = true"
          @save="save"
          @downloadVue="downloadVue"
        />
      </el-col>
      <el-col :span="6">
        <AttrFrom
          :fields="fields"
          :form="formcom"
          :form-setting="formSetting"
          :drag2="drag2"
          @deleteComponent="deleteComponent"
        />
      </el-col>
    </el-row>
    <el-dialog
      v-if="dialogVisible"
      title=""
      :visible.sync="dialogVisible"
      width="70%"
      destroy-on-close
    >
      <show-from ref="showForm" :form-data="{formSetting:formSetting,forms:data}" form-ref-name="showForm" />
      <el-button @click="submit"></el-button>
    </el-dialog>
  </div>
</template>

<script>
import dow from './dow'
import Assembly from './assembly.vue'
import Forms from './forms'
import AttrFrom from './attributeform'
import ShowFrom from './formShow'
export default {
  name: 'DfFormDesign',
  components: {
    Assembly, Forms, AttrFrom, ShowFrom
  },
  props: {
    formData: {
      type: Object,
      default() {
        return {}
      }
    },
    fields: {
      type: Array,
      default: () => {
        return [
          { label: '', value: 'name' },
          { label: '', value: 'age' }
        ]
      },
      required: true
    }
  },
  data() {
    return {
      //  1   2   3 4 
      drag1: '1',
      //  1   2   3 
      drag2: '1',
      //  
      com: {},
      //   
      delcom: {},
      // 
      data: [],
      // 
      formSetting: {
        labelPosition: 'left',
        labelWidth: 80,
        formSize: 'small'
      },
      // 
      formcom: {
        showFrom: [],
        rules: []
      },
      dialogVisible: false
    }
  },
  created() {
    // props  data
    if (this.formData && this.formData.formSetting && this.formData.forms) {
      this.formSetting = this.formData.formSetting
      this.data = this.formData.forms
    }
  },
  mounted() {
  },
  methods: {
    //  
    reset() {
      this.drag1 = '1'
      this.drag2 = '1'
      // 
      this.$refs.forms.chonzhihxindex()
    },
    // 
    setDrag(data) {
      this[data.type] = data.value
    },
    // 
    setcom(data) {
      this.com = data
    },
    setdelcom(data) {
      this.delcom = data
    },
    setformcom(data) {
      this.formcom = data
    },
    // 
    deleteComponent() {
      //    
      if (this.delcom.id === this.formcom.id) {
        this.formcom = {
          showFrom: [],
          rules: []
        }
      }
      this.data = this.data.filter(item => item.id !== this.delcom.id)
    },
    // 
    addcom(index = -1) {
      if (index === -1) {
        this.data.push(this.com)
      } else {
        this.data.splice(index, 0, this.com)
      }
    },
    // 
    exchange(start, end) {
      const arr = this.data
      // JSON.parse(JSON.stringify(this.data))
      const aa = arr[start]
      arr[start] = arr[end]
      arr[end] = aa
      this.data = arr
    },
    save() {
      this.$emit('save', { formSetting: this.formSetting, forms: this.data })
    },
    // 
    download(filename, text) {
      const element = document.createElement('a')
      element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text))
      element.setAttribute('download', filename)
      element.style.display = 'none'
      document.body.appendChild(element)
      element.click()
      document.body.removeChild(element)
    },
    downloadVue() {
      const filename = 'hello.vue'
      const text = dow(this.formSetting, this.data)
      console.log(text)
      this.download(filename, text)
    },
    submit() {
      const [validPromise, formData] = this.$refs.showForm.submitForm()
      validPromise.then(valid => {
        console.log(valid)
        console.log(formData)
      }).catch(e => {
        console.log(e)
      })
    }
  }
}
</script>

