
import assembly from './assembly'
export default class MCheckbox extends assembly {
  constructor(
    type = 'Checkbox',
    name = '',
    col = 24,
    key = 'Checkbox' + new Date().getTime()
  ) {
    super(type, name, col, key)
    this.showFrom = ['name', 'col', 'key',
      'disabled', 'checkboxType', 'dataType', 'option', 'url', 'min', 'max'
    ]
    // button
    this.checkboxType = 'fang'
    this.disabled = false
    this.min = 0
    this.max = 2

    //  option  url
    this.dataType = 'option'
    // 
    this.url = ''
    // 
    this.option = [{
      value: 'Beijing',
      label: ''
    }, {
      value: 'Shanghai',
      label: ''
    }, {
      value: 'Tianjin',
      label: ''
    }, {
      value: 'ChongQing',
      label: ''
    }]
    // 
    this.rules = [
      { required: false, message: '' + this.name, trigger: 'change' }
    ]
  }

  deleteOption(index) {
    this.option.splice(index, 1)
  }
  addoption() {
    this.option.push({ label: '', value: 'newValue' })
  }

  deleteRules(index) {
    this.rules.splice(index + 1, 1)
  }
  addRules(data) {
    this.rules.push(data)
  }
}
