import DfModal from './df-modal/DfModal'
import DfFormDesign from './df-form/formDesign'
import DfFormShow from './df-form/formShow'
import './fonts/font.scss'
// 
const components = [DfModal, DfFormDesign, DfFormShow]

//  install  Vue  use 
const install = function(Vue, options = { key: '', router: {}}) {
  const { key = 'cacheTo', router } = options
  // 
  components.forEach(function(item) {
    if (item.install) {
      Vue.use(item)
    } else if (item.name) {
      Vue.component(item.name, item)
    }
  })
  Vue.use({ key, router })
}

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}
export { DfModal, DfFormDesign, DfFormShow }
export default {
  //  install Vue.use() 
  version: '0.2.1',
  install
}
