
import assembly from './assembly'
export default class MSelect extends assembly {
  constructor(
    type = 'Select',
    name = '',
    col = 24,
    key = 'Select' + new Date().getTime()
  ) {
    super(type, name, col, key)
    this.showFrom = ['name', 'col', 'key', 'multiple', 'collapseTags', 'placeholder',
      'clearable', 'disabled', 'filterable', 'allowCreate', 'dataType', 'option', 'url',
      'optionLabelKey', 'optionValueKey', 'relatedField'
    ]
    this.multiple = false
    this.collapseTags = false
    this.disabled = false
    this.filterable = false
    this.allowCreate = false
    this.placeholder = '' + this.name
    this.clearable = true
    //  option  url
    this.dataType = 'option'
    // 
    this.url = ''
    // 
    this.option = [{
      value: 'Beijing',
      label: ''
    }, {
      value: 'Shanghai',
      label: ''
    }, {
      value: 'Tianjin',
      label: ''
    }, {
      value: 'ChongQing',
      label: ''
    }]
    // 
    this.rules = [
      { required: false, message: '' + this.name, trigger: 'change' }
    ]
    // 
    this.optionLabelKey = ''
    this.optionValueKey = ''
    // 
    this.relatedField = ''
  }

  deleteOption(index) {
    this.option.splice(index, 1)
  }
  addoption() {
    this.option.push({ label: '', value: 'newValue' })
  }
  deleteRules(index) {
    this.rules.splice(index + 1, 1)
  }
  addRules(data) {
    this.rules.push(data)
  }
}
