    },
    zzcindex() {
      return this.drag2 === '1' ? -10 : 10
    }
  },
  watch: {
    form: function() {
      const thiz = this
      this.formclass = 'animated bounceInRight'
      const timer = setTimeout(function() {
        thiz.formclass = ''
        window.clearTimeout(timer)
      }, 1000)
      console.log('bianhua')
    }
  },
  created() {
  },
  methods: {
    drop() {
      this.lj = false
      this.$emit('deleteComponent')
    },
    dragover() {
      this.lj = true
      // console.log('')
    },
    dragleave() {
      this.lj = false
      // console.log('')
    },
    // 
    handleDelete(index) {
      this.form.deleteOption(index)
    },
    addOption() {
      this.form.addoption()
    },
    saveRegular() {
      const { pattern, message } = this.regular
      if (!pattern || !message) {
        this.$message.error('!')
        return
      }
      let isReg
      try {
        // eslint-disable-next-line no-eval
        isReg = eval(pattern) instanceof RegExp
      } catch (e) {
        isReg = false
      }
      if (isReg === true) {
        const aa = pattern.substring(1, pattern.length - 1)
        const data = {
          'pattern': new RegExp(aa),
          'message': message
        }
        this.form.addRules(data)
        this.dialogVisible = false
      } else {
        this.$message.error('!')
      }
    }
  }
}
</script>

