
import assembly from './assembly'
export default class MRadio extends assembly {
  constructor(
    type = 'Radio',
    name = '',
    col = 24,
    key = 'Radio' + new Date().getTime()
  ) {
    super(type, name, col, key)
    this.showFrom = ['name', 'col', 'key',
      'disabled', 'radioType', 'dataType', 'option', 'url'
    ]
    // button
    this.radioType = 'yuan'
    this.disabled = false

    //  option  url
    this.dataType = 'option'
    // 
    this.url = ''
    // 
    this.option = [{
      value: 'Beijing',
      label: ''
    }, {
      value: 'Shanghai',
      label: ''
    }, {
      value: 'Tianjin',
      label: ''
    }, {
      value: 'ChongQing',
      label: ''
    }]
    // 
    this.rules = [
      { required: false, message: '' + this.name, trigger: 'change' }
    ]
  }

  deleteOption(index) {
    this.option.splice(index, 1)
  }
  addoption() {
    this.option.push({ label: '', value: 'newValue' })
  }
  deleteRules(index) {
    this.rules.splice(index + 1, 1)
  }
  addRules(data) {
    this.rules.push(data)
  }
}
