<template>
  <el-card class="elcard">
    <div slot="header" class="clearfix">
      <span></span>
      <el-button style="float: right; padding: 3px 0;margin-left:5px" type="text" @click="save"></el-button>
      <el-button style="float: right; padding: 3px 0;margin-left:5px" type="text" @click="preview"></el-button>
      <el-button style="float: right; padding: 3px 0;margin-left:5px" type="text" @click="()=>this.$emit('downloadVue')"></el-button>
    </div>
    <div
      class="elcardzz"
      :style="styleCard"
      @dragover="dragover"
      @dragleave="dragleave"
      @drop="dropfun"
      @dragover.prevent
    />
    <!-- <span>{{drag1}} {{drag2}} {{startIndex}} {{endIndex}}</span> -->
    <div class="forms-content">
      <!--  -->
      <div v-if="hxindex === -1" class="hxdiv" />
      <el-form
        :model="from"
        :label-position="formSetting.labelPosition"
        :label-width="formSetting.labelWidth+'px'"
        :size="formSetting.formSize"
      >
        <el-row :gutter="10">
          <el-col v-for="(item,index) in fromData" :key="index" :span="item.col">
            <div
              class="form-item"
              :style="{'background': startIndex === index ? 'rgba(255,0,0,0.2)' : endIndex === index ? 'rgba(0,0,255,0.2)' :''}"
            >
              <div
                class="form-item2"
                :style="{...styleitem,border: actId === item.id ? '1px solid red' : ''}"
                draggable="true"
                @dragstart="dragstart(index,item)"
                @dragend="dragend(index)"
                @dragover="dragover2(index)"
                @dragleave="dragleave2(index)"
                @drop="dropfun2(index)"
                @dragover.prevent
                @click="clickItem(item)"
              >
                <!-- <div class="form-itemzz"></div> -->
                <div v-if="hxindex === index" class="hxdiv" />

                <el-form-item
                  v-if="['Divider','p'].indexOf(item.type) === -1 "
                  v-model="from[item.key]"
                  :label="item.name"
                  :prop="item.key"
                  :rules="item.rules"
                >
                  <FormItem :item="item" :form="from" />
                </el-form-item>
                <el-divider v-if="item.type === 'Divider'" :content-position="item.contentPosition">{{ item.text }}</el-divider>
                <p v-if="item.type === 'p'" :style="{'text-align': item.contentPosition,'font-size':item.fontsize+'px',color:item.textColor}">{{ item.text }}</p>
              </div>
            </div>
          </el-col>
        </el-row>
      </el-form>
      <div v-if="hxindex === -2" class="hxdiv" />
    </div>
    <!-- <el-button @click="sub">sub</el-button> -->
  </el-card>
</template>

<script>
import FormItem from './formitem'
export default {
  name: 'Forms',
  components: {
    FormItem
  },
  // 
  props: {
    drag1: {
      type: String,
      default: () => {
        return '1'
      }
    },
    drag2: {
      type: String,
      default: () => {
        return '1'
      }
    },
    fromData: {
      type: Array,
      default: () => {
        return [{ name: '111' }]
      }
    },
    formSetting: {
      type: Object,
      default: () => {
        return {
          labelPosition: 'left',
          labelWidth: 80,
          formSize: 'small'
        }
      }
    },
    // 
    com: {
      type: Object,
      default() {
        return {}
      }
    },
    height: {
      type: Number,
      default: () => {
        return 600
      }
    }
  },
  data() {
    return {
      startIndex: -1,
      endIndex: -1,
      // 
      from: {},
      // -3  -2 div -1 div   
      hxindex: -3,
      // id
      actId: '0'
    }
  },
  computed: {
    // div
    styleCard: function() {
      // drag1  1  2   3 4 
      // drag2  1   2   3 
      const sty = { background: 'rgb(0,0,0,0)' }
      if (this.drag1 === '2') {
        // sty.background = 'rgb(0,0,0,0.1)'
      } else if (this.drag1 === '3') {
        // sty.background = 'rgb(0,0,0,0.5)'
        // sty["z-index"] = 10;
      }
      return sty
    },
    styleitem: function() {
      const sty = { 'z-index': 100 }
      // if (this.drag2 == '2') {
      // }
      return sty
    }
  },
  watch: {
    // fromData 
    fromData() {
      this.initFrom()
    }
    // fromData: {
    //   handler() {
    //   },
    //   deep: true
    // }
  },
  created() {
    this.initFrom()
  },
  methods: {
    // Checkbox 
    initFrom() {
      const obj = {}
      this.fromData.map(item => {
        if (item.type === 'Checkbox') {
          obj[item.key] = []
        }
      })
      this.from = obj
    },
    chonzhihxindex() {
      this.hxindex = -3
    },
    // div 
    dragover() {
      // 
      if (this.drag2 === '1') {
        this.$emit('setdrag', { type: 'drag1', value: '3' })
        //   
        if (this.fromData.length === 0) {
          this.hxindex = -1
        } else {
          this.hxindex = -2
        }
      }
    },
    // div
    dragleave() {
      // div  div  
      if (this.drag1 === '4' || this.drag2 !== '1') {
        return
      }
      this.$emit('setdrag', { type: 'drag1', value: '2' })
      this.hxindex = -3
    },
    // div 
    dropfun() {
      //  
      if (this.drag2 === '1') {
        this.$emit('addcom')
        this.hxindex = -3
      }
    },
    // 
    dragstart(index, data) {
      this.$emit('setdrag', { type: 'drag2', value: '2' })
      this.$emit('setdelcom', data)
      this.startIndex = index
      console.log(' div', index)
    },
    dragend() {
      // 
      console.log('end ------------')
      this.startIndex = -1
      this.endIndex = -1
      this.$emit('reset')
    },
    // 
    dragover2(index) {
      //  
      if (this.drag2 === '1' && this.drag1 !== '1') {
        this.$emit('setdrag', { type: 'drag1', value: '4' })
        this.hxindex = index
        // todo 
      }
      //  
      if (this.drag1 === '1' && this.drag2 !== '1') {
        if (index !== this.startIndex) {
          this.endIndex = index
          this.$emit('setdrag', { type: 'drag2', value: '3' })
        } else {
          this.endIndex = -1
        }
      }
    },
    dragleave2(index) {
      this.endIndex = -1
    },
    dropfun2(index) {
      // 
      if (this.drag1 === '1') {
        this.$emit('setdrag', { type: 'drag2', value: '1' })
        if (
          this.startIndex !== this.endIndex &&
          this.startIndex !== -1 &&
          this.endIndex !== -1
        ) {
          this.$emit('exchange', this.startIndex, this.endIndex)
        }
      }
      // div   
      if (this.drag1 === '4') {
        this.$emit('addcom', index)
        this.hxindex = -3
      }
    },
    // 
    clickItem(data) {
      console.log('data..')
      console.log(data)
      this.actId = data.id
      this.$emit('setformcom', data)
    },
    // 
    preview() {
      this.$emit('setDialogVisible', true)
    },
    // 
    save() {
      this.$emit('save')
    }
  }
}
</script>

<style scoped>
.elcard {
  position: relative;
  min-height: 600px;
}

.elcardzz {
  position: absolute;
  top: 58px;
  left: 0;
  right: 0;
  bottom: 0;
}

.forms-content {
  min-height: 600px;
}
.form-item {
  position: relative;
  padding: 3px;
  overflow: hidden;
  border-radius: 3px;
  box-sizing: border-box;
}
.form-item2 {
  position: relative;
  padding: 3px;
  overflow: hidden;
  border-radius: 3px;
  box-sizing: border-box;
  border: 1px dashed #3a88ed;
  cursor: move;
}
.form-item2:hover {
  border: 1px dashed red;
  transition: 0.5s;
}
.form-itemzz {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.hxdiv {
  width: 100%;
  height: 2px;
  background: dodgerblue;
  border-radius: 2px;
}

.icons1 {
  position: absolute;
  top: 0;
  right: 0;
}
</style>
