
import assembly from './assembly'
export default class MTextarea extends assembly {
  constructor(
    type = 'Textarea',
    name = '',
    col = 24,
    key = 'Textarea' + new Date().getTime(),
    rows = 2
  ) {
    super(type, name, col, key)
    this.showFrom = ['name', 'col', 'key', 'rows', 'minlength', 'maxlength', 'showWordLimit', 'placeholder',
      'clearable', 'disabled'
    ]
    this.rows = rows
    this.minlength = 0
    this.maxlength = 100
    this.showWordLimit = true
    this.placeholder = '' + this.name
    this.clearable = true
    this.disabled = false
    // 
    this.rules = [
      { required: false, message: '' + this.name, trigger: 'change' }
    ]
  }
  deleteRules(index) {
    this.rules.splice(index + 1, 1)
  }
  addRules(data) {
    this.rules.push(data)
  }
}
